<template>
<div>
    <v-main>
        <v-layout style="background:#fff" class="py-5 px-3" align-center justify-center wrap column>
            <v-flex xs12 md12>
                <v-layout wrap>
                    <v-flex xs12 md1></v-flex>
                    <v-flex xs12 md7 pr-5>
                        <p class="heading">Blood</p>
                        <p class="text">Donate your blood for a reason, let the reason to be life. </p>
                        <v-btn color="red" class="white--text mb-3" href="https://docs.google.com/forms/d/e/1FAIpQLSfsAl7im7zUvhj7I9KgcnBah-MEuhwg_xRJXuxzD4yZaT2lJQ/viewform?usp=pp_url" target="_blank">Donate Blood</v-btn>
                        <p class="heading">Privacy Policy</p>
                        <p class="text">
                            Your information is collected to verify your existence and we will not be shared anywhere else without your consent.
                        </p>
                    </v-flex>
                    <v-flex xs12 md4 hidden-sm-and-down>
                        <v-img :src="contactuslogo" height="auto" width="50%"></v-img>
                    </v-flex>
                </v-layout>
            </v-flex>
        </v-layout>
    </v-main>
    <iframe name="iframe" style="display:none"></iframe>
    <v-layout row wrap align-center justify-center mt-3>
        <v-flex xs10 md8 text-center>
            <v-card class="mx-auto elevation-15" style="border-radius:8px" v-if="status">
                <v-card-text class="heading">
                    <span style="color:#FBBD12">Request</span>
                    <span style="color:red">Blood</span>
                </v-card-text>
                <v-card-text>
                    <v-form ref="form" v-model="valid" action="https://docs.google.com/forms/u/4/d/e/1FAIpQLSfbGtwcJ41b7RSI7bOHOB0bQz6KuS_ZEHj6O7cVMg5FT9Jn3g/formResponse" method="POST" target="iframe">
                        <v-text-field :value="name" type="text" label="Name" name="entry.2038454249" counter :rules="nameRules" color="blue"></v-text-field>
                        <v-text-field :value="value" label="Phone (prefer whatsapp)" name="entry.964058731" color="green" counter :rules="phoneRules"></v-text-field>
                        <v-text-field :value="value" label="Blood Type with Units" type="email" name="entry.15560267" color="yellow" counter :rules="nameRules"></v-text-field>
                        <v-textarea :value="value" label="Place" type="text" name="entry.1710941217" color="red" counter :rules="nameRules"></v-textarea>
                        <v-textarea :value="value" label="Reason" type="text" name="entry.562214039" color="blue" counter :rules="nameRules"></v-textarea>
                        <v-text-field :value="value" label="When you need Blood?" type="text" name="entry.1028089604" color="red" counter :rules="nameRules"></v-text-field>
                        <v-btn @click="callme" :disabled="!valid" type="submit" color="primary" v-show="show">{{text}}</v-btn>

                        <v-btn @click="callme2" color="red lighten-1" class="white--text" v-show="!show">Clear All</v-btn>
                    </v-form>
                </v-card-text>
            </v-card>
        </v-flex>
    </v-layout>

    <offline @detected-condition="handleConnectivityChange">
        <!-- Only renders when the device is online -->
        <div slot="online"></div>
        <!-- Only renders when the device is offline -->
        <div slot="offline"></div>
    </offline>

    <v-snackbar v-model="snackbar" color="green" :timeout="timeout">
        Thanks For your Response.
        We will respond you soon...!
        <v-btn color="white" @click="callme1" small>Clear</v-btn>
    </v-snackbar>
    <v-snackbar v-if="!status" v-model="checknetwork" color="red" :timeout="timeoutstatus">You are Offline, Check Your Network Connection.</v-snackbar>
</div>
</template>

<script>
import contactus from "@/assets/logos/joinus.svg";
import offline from "v-offline";

export default {
    components: {
        offline
    },
    data() {
        return {
            value1: "",
            value: "",
            valueother: "",
            snackbar: false,
            other: this.valueother == "Others" ? true : false,
            valid: true,
            text: "submit",
            show: true,
            timeout: 8000,
            timeoutstatus: 0,
            contactuslogo: contactus,
            checknetwork: true,
            name: "",
            nameRules: [v => !!v || "Required field"],
            status: true,
            phoneRules: [
                (v) => !!v || 'Name is required',
                (v) => v && v.length <= 10 || 'Number must be less than 10 characters'
            ],
            items: ["Friend", "Google", "Social media", "News Article", "Others"]
        };
    },
    mounted() {
    document.title = "Rotract | Blood";
  },
    updated() {
        if (this.valueother == "Others") {
            this.other = true
        } else {
            this.other = false
        }
    },
    methods: {
        handleConnectivityChange(status) {
            this.status = status;
        },
        callme() {
            this.text = "submit";
            this.snackbar = true;
            this.show = false;
        },
        callme1() {
            this.text = "submit";
            this.snackbar = false;
            this.$refs.form.reset();
            this.show = true;
        },
        callme2() {
            this.show = true;
            this.$refs.form.reset();
        }
    }
};
</script>

<style scoped>
.heading {
    font-size: 28px;
    color: #212529;
    font-family: "Josefin Sans", sans-serif;
}

.text {
    font-size: 16px;
    color: #212529;
    font-family: "Josefin Sans", sans-serif;
    text-align: justify;
    line-height: 1.8rem;
}
</style>
